var rotate_img = $('.rotate-img');
(function($) {
    $.fn.hasScrollBar = function() {
        return this.get(0).scrollHeight > this.height();
    }
})(jQuery);






(function($, undefined) {
  $.fn.loopScroll = function(p_options) {
    var options = $.extend({
        direction: "upwards",
        speed: 60
    }, p_options);

    return this.each(function() {



  
    var elementss_c = $('.missionKit_descr').length;
        var elementss_f = parseInt($('.missionKit_descr').css('font-size'));
      var elementss_l =  parseInt($('.missionKit_descr').css('line-height'));
      var elementss_h = $('.text').height();
      var numberLines = parseInt(elementss_h/(elementss_f + elementss_l) + elementss_c);
      console.log(numberLines);
     



     
          



   


      var obj = $(this).find(".news");
      var text_height = obj.find(".text").height();
      var mission_kit = obj.find(".missionKit_descr").height();
      
      var marg = parseInt(obj.find('.mission-kit').css('margin-bottom'));
      var line = parseInt(obj.find('.missionKit_descr').css('line-height')) / numberLines;


   

     
   
    
      var start_y, end_y;
      if (options.direction == "downwards") {
        start_y = -text_height;
        end_y = 0;
      } else if (options.direction == "upwards") {
        start_y = 0;
        end_y = -text_height;
      }

      var animate = function() {
        // setup animation of specified block "obj"
        // calculate distance of animation    
        var distance = Math.abs(end_y - parseInt(obj.css("top")));
          
          //alert("animate " + obj.css("top") + "-> " + end_y + " " + distance);
           
        //duration will be distance / speed
        obj.animate(
          { top: end_y },  //scroll upwards
          1000 * distance / options.speed,
          "linear",
          function() {
              // scroll to start position
              obj.css("top", start_y + marg + line);
              animate();    
          }
        );
      };

      obj.find(".text").clone().appendTo(obj);
      $(this).on("mouseover", function() {
        obj.stop();
        rotate_img.css('animation-play-state', 'paused');
      }).on("mouseout", function() {
        animate(); // resume animation
        rotate_img.css('animation', 'rot linear infinite 30s');
      });
      obj.css("top", start_y);
      animate(); // start animation
        
    });


  };
}(jQuery));

