'use strict';
$(document).ready(function(){
	
$(".go-to").on("click",function(e){
	e.preventDefault();
	var t=$(this).attr("href");
	if($(t).length){
		var s=$(t).offset().top;
		$("body,html").stop().animate({scrollTop:s},1500)
	}else console.warn("ID don't search!")});



		$(window).on('scroll load', function() {
		if ($(this).scrollTop() > 1){
		$('.navigation').addClass("fixed");
			}
		else{
	$('.navigation').removeClass("fixed");
}	
});


$(".nav-group li").on('click', function(){
	$(this).addClass('is-selected');
	$(".nav-group li").not(this).removeClass('is-selected');
});


$('.formats_card-text').on('scroll',function(){
  if(this.scrollTop==this.scrollHeight-this.clientHeight){
    $(this).closest('.formats_card').find('.format_cars-beforeShadow').fadeOut(400);
  }
  else {
    $(this).closest('.formats_card').find('.format_cars-beforeShadow').fadeIn(400);
  }
})


})


$(document).ready(function () {
	$('.benefits__number-2').hide();
 
    var show = true;
    var countbox = ".made";
    $(window).on("scroll load resize", function () {
        if (!show) return false; // Отменяем показ анимации, если она уже была выполнена
        var w_top = $(window).scrollTop(); // Количество пикселей на которое была прокручена страница
        var e_top = $(countbox).offset().top; // Расстояние от блока со счетчиками до верха всего документа
        var w_height = $(window).height(); // Высота окна браузера
        var d_height = $(document).height(); // Высота всего документа
        var e_height = $(countbox).outerHeight(); // Полная высота блока со счетчиками
        if (w_top + 500 >= e_top || w_height + w_top == d_height || e_height + e_top < w_height) {
            $('.benefits__number').css('opacity', '1');
            $('.benefits__number-2').fadeIn(3500);
            $('.benefits__number').spincrement({
                thousandSeparator: "",
                duration: 3500,
                from: 0
            });


             
            show = false;
        }
    });

      new WOW().init();

});


