$(document).ready(function(){
  $('.show-menu-btn').on('click', function() {
        $(this).toggleClass('anim-burger');
        $(this).find('.ico-box').toggleClass('ico-anim');
        $('.navigation').toggleClass('active-menu');
        $('.overlay-menu').toggleClass('active-menu');
        $('body').toggleClass('menu-opened');
        return false;
    });
    $('.overlay-nav li a').on('click', function () {
        $('.navigation').removeClass('active-menu');
        $('.show-menu-btn').removeClass('open');
        $('.overlay-menu').removeClass('active-menu');
        $('body').removeClass('menu-opened');
        return false;
    });
    $(window).keydown(function(e) {
        if ($('.overlay-menu').hasClass('active-menu')) {
            if (e.which === 27) {
                $('.navigation').removeClass('active-menu');
                $('.show-menu-btn').removeClass('open');
                $('.overlay-menu').removeClass('active-menu');
                $('body').removeClass('menu-opened');
            }
        }
    });

     Waves.init();
})